import React from 'react';
import AbstractForm from '../abstract/form';
import { connect } from 'react-redux';

class OrderAdd extends AbstractForm {
    constructor(props)
    {
        super(props);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleSubmit(e)
    {
        e.preventDefault();
        this.setState({loading: true});

        let _this = this;
        let data = {
            'address': this.addressInput.value,
            'phone': this.phoneInput.value,
            'comment': this.commentInput.value ? this.commentInput.value : null,
            'pay_type': 'money', //this.typeSelect.value,
        };

        this.state.request(
            'order/',
            'POST',
            data,
            {},
            function (response)
            {
                _this.setState({loading: false});
                _this.props.history.push('/user/order');
            },
            this.state.errorCallback
        );
    }

    viewForm() {
        return (
            <div className="w-75">
                <h4 className="text-center">   :</h4>
                <p className={'text-left'}>
                    : { this.props.user.fio } <br/>
                    Email: {this.props.user.email}
                </p>
                <form onSubmit={this.handleSubmit}>
                    <input
                        name="email"
                        type="text"
                        required={true}
                        placeholder={":*"}
                        defaultValue={this.props.user.phone}
                        minLength={5}
                        className={'form-control '}
                        ref={(input) => {this.phoneInput = input}}
                    />
                    <input
                        name="fio"
                        type="text"
                        required={true}
                        placeholder={":*"}
                        defaultValue={this.props.user.address}
                        className={'form-control '}
                        ref={(input) => {this.addressInput = input}}
                    />
                    <input
                        name="comment"
                        type="text"
                        required={false}
                        maxLength={250}
                        placeholder={"  "}
                        className={'form-control '}
                        ref={(input) => {this.commentInput = input}}
                    />
                    <p className={'text-left'}>
                          .  3000   (  ).<br/>
                            /.<br/>
                               ( ) 
                    </p>
                    {/*<label> </label>*/}
                    {/*<select className={'form-control'} ref={(input) => {this.typeSelect = input}}>*/}
                    {/*    <option value={'card'}> </option>*/}
                    {/*    <option value={'money'}> </option>*/}
                    {/*</select>*/}
                    <br/>
                    <p className="text-center">
                        <button type="submit" className="btn btn-success">
                            <i className={'fa fa-check'}> <span> </span></i>
                        </button>
                    </p>
                </form>
            </div>
        );
    }
}

export default connect(
    state => ({
        user: state.user,
    }),
    dispatch => ({

    })
)(OrderAdd);